@import "./_variables.scss";

@mixin contentSize {
  padding-top: $headerHeight;
  height: 100%;
  width: 95%;
  max-width: 128rem;
}

@mixin imageText($image) {
  background-image: $image;
  color: transparent;
  background-clip: text;
}

@mixin center($top: 50%, $left: 50%) {
  position: absolute;
  top: $top;
  left: $left;
  transform: translate(-50%, -50%);
}

@mixin fontTranslate($x, $y, $size: 0.85, $unit: "viewport") {
  font-size: #{$size}rem;
  @if $unit == "viewport" {
    transform: translate(#{$x}vw, #{$y}vh);
  } @else {
    transform: translate(#{$x}#{$unit}, #{$y}#{$unit});
  }
}

@mixin maxWidth($breakpoint) {
  @if $breakpoint == l {
    @media only screen and (max-width: $bp-l) {
      @content;
    }
  }

  @if $breakpoint == m {
    @media only screen and (max-width: $bp-m) {
      @content;
    }
  }

  @if $breakpoint == s {
    @media only screen and (max-width: $bp-s) {
      @content;
    }
  }

  @if $breakpoint == xs {
    @media only screen and (max-width: $bp-xs) {
      @content;
    }
  }
}
