@import "../../scss/_mixin.scss";

.container {
  width: 100%;
  height: 1100vh;
  position: relative;
}

.animationContainer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &.absolute {
    position: absolute;
  }
}

.flexContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  .light {
    position: absolute;
    background-color: #0f0;
    border-radius: 50%;
    animation: lightColor 3.5s linear infinite, lightSize 5s linear infinite;

    &.hide {
      visibility: hidden;
      animation: unset;
    }
  }
}

.cubeContainer {
  position: absolute;
  top: -50vh;
  left: -50vw;
}

.row {
  display: inline-flex;
  margin-top: -3.2rem;
  margin-left: -5rem;

  &:nth-child(even) {
    margin-left: 0.2rem;
  }
}

.hexagon {
  width: 10rem;
  height: 11rem;
  background-color: #000;
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  margin: 0.2rem;
}

.computer {
  position: absolute;
  width: 12rem;
  max-height: 12rem;
}
.screen {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  top: -5rem;
  left: -5.4rem;
  width: 11.2rem;
  height: 6.7rem;
  padding: 0;
}

.code {
  position: absolute;
  font-size: 0.85rem;
  margin-right: 0.5rem;
  padding-bottom: 0.37rem;
  font-family: Monospace;
  color: #14c18b;
  font-family: "Source Code Pro", monospace;
  @for $i from 1 through 15 {
    &:nth-child(#{$i}) {
      animation: fadeIn#{$i} 1s ease-in infinite;
      animation-play-state: paused;
      animation-delay: calc(var(--scroll) * -1s);
    }
  }
}
@keyframes lightColor {
  0% {
    filter: hue-rotate(0deg) blur(50px);
  }
  100% {
    filter: hue-rotate(360deg) blur(50px);
  }
}

@keyframes lightSize {
  0%,
  100% {
    width: 10rem;
    height: 10rem;
  }
  50% {
    width: 28rem;
    height: 28rem;
  }
}
